html,body{margin:0;background-color:gray}canvas{display:block;vertical-align:bottom}#particles-js{position:absolute;width:100%;height:100%;background-color:#2c2e43;background-repeat:no-repeat;background-size:cover;background-position:50% 50%}.github-component{font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol !important}.repo-language-color{filter:brightness(125%) !important;border-radius:50%;display:inline-block;height:12px;position:relative;top:2px;width:12px}.emoji{width:18px;height:auto;vertical-align:middle}.article h1,.article h2,.article h3,.article h4,.article .highlight{margin-bottom:16px}.article blockquote{color:#6a737d;border-left:2px solid #959da5;padding-left:16px;margin-bottom:16px}.article ul,.article ol{padding-left:32px;margin-bottom:16px}.article li ul,.article li ol{padding-left:16px;margin-bottom:0px}.min-height-full{min-height:100vh}